.collection-item {
  width: 22%;
  display: flex;
  flex-direction: column;
  height: 350px;
  align-items: center;

  .image {
    width: 100%;
    height: 95%;
    background-size: cover;
    background-position: center;
    margin-bottom: 5px;
  }

  .collection-footer {
    width: 100%;
    height: 8%;
    display: flex;
    justify-content: space-between;
    font-size: 18px;
    background-color: whitesmoke;

    &:hover {
      font-size: 20px;
      font-weight: 600;
      color: whitesmoke;
      background-color: #206161;
    }

    .name {
      width: 70%;
      margin-bottom: 15px;
    }

    .price {
      width: 20%;
    }
  }
}
